import { useState, useRef, useEffect } from 'react';
import { useSearchParams } from "next/navigation";
import { WheelRewardType, SegmentType, SpinResponseType } from "@/types/type"

export default function GameSpinner() {
    const searchParams = useSearchParams();
    
    const userId = searchParams.get("userId");

    const API_BASE_URL: string = 'http://localhost:8080/api';

    const [rotation, setRotation] = useState<number>(0);
    const [isSpinning, setIsSpinning] = useState<boolean>(false);
    const [result, setResult] = useState<number | null>(null);
    const [totalScore, setTotalScore] = useState<number>(0);
    const [segments, setSegments] = useState<SegmentType[]>([]);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string>('');

    const spinIntervalRef = useRef<NodeJS.Timeout | null>(null);
    const decelerationRef = useRef<NodeJS.Timeout | null>(null);

    useEffect(() => {
        fetchWheelRewards();
    }, []);

    const fetchWheelRewards = async (): Promise<void> => {
        try {
            setLoading(true);
            setError('');
            const response = await fetch(`${API_BASE_URL}/wheel-reward`);

            if (!response.ok) {
                throw new Error('Failed to fetch wheel rewards');
            }

            const rewards: WheelRewardType[] = await response.json();

            const activeRewards = rewards.filter(r => r.isActive);
            const segmentData: SegmentType[] = activeRewards.map((reward) => ({
                id: reward.id,
                points: reward.point,
                color: '#640000',
                label: reward.point.toString()
            }));

            setSegments(segmentData);
            setLoading(false);
        } catch (err) {
            setError(err instanceof Error ? err.message : 'Failed to load rewards');
            setLoading(false);
        }
    };

    const saveSpinResult = async (points: number): Promise<void> => {
        try {
            const req = {
                user_id: userId,
                point: points
            }
            const response = await fetch(`${API_BASE_URL}/spin`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(req)
            });

            if (!response.ok) {
                throw new Error('Failed to save spin result');
            }

            const data: SpinResponseType = await response.json();
            setTotalScore(data.totalPoint);

        } catch (err) {
            console.error('Error saving spin result:', err);
            setError('Failed to save result. Points not recorded.');
        }
    };

    const startSpin = (): void => {
        if (isSpinning || segments.length === 0) return;

        setIsSpinning(true);
        setResult(null);
        setError('');

        const currentSpeed = 20;

        spinIntervalRef.current = setInterval(() => {
            setRotation(prev => (prev + currentSpeed) % 360);
        }, 16);
    };

    const stopSpin = (): void => {
        if (!isSpinning) return;

        if (spinIntervalRef.current) {
            clearInterval(spinIntervalRef.current);
        }

        let currentSpeed = 20;
        const deceleration = 0.3;

        decelerationRef.current = setInterval(() => {
            currentSpeed -= deceleration;

            if (currentSpeed <= 0) {
                if (decelerationRef.current) {
                    clearInterval(decelerationRef.current);
                }
                setIsSpinning(false);
                calculateResult();
            } else {
                setRotation(prev => (prev + currentSpeed) % 360);
            }
        }, 16);
    };

    const calculateResult = async (): Promise<void> => {
        let normalizedRotation = rotation % 360;
        if (normalizedRotation < 0) normalizedRotation += 360;

        const segmentAngle = 360 / segments.length;
        const adjustedRotation = (360 - normalizedRotation + segmentAngle / 2) % 360;
        let segmentIndex = Math.floor(adjustedRotation / segmentAngle) % segments.length;

        if (segmentIndex < 0) segmentIndex += segments.length;
        if (segmentIndex >= segments.length) segmentIndex = 0;

        const won = segments[segmentIndex];

        console.log('Rotation:', normalizedRotation.toFixed(2), 'Adjusted:', adjustedRotation.toFixed(2), 'Index:', segmentIndex, 'Won:', won.points);

        setResult(won.points);

        await saveSpinResult(won.points);
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-purple-600 to-blue-600 flex items-center justify-center">
                <div className="text-white text-2xl">Loading wheel...</div>
            </div>
        );
    }

    if (segments.length === 0) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-purple-600 to-blue-600 flex items-center justify-center">
                <div className="bg-white rounded-3xl shadow-2xl p-8 max-w-md w-full text-center">
                    <p className="text-xl text-red-600 mb-4">No active rewards available</p>
                    <button
                        onClick={fetchWheelRewards}
                        className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-6 rounded-lg"
                    >
                        Retry
                    </button>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gradient-to-br from-purple-600 to-blue-600 flex items-center justify-center p-4">
            <div className="bg-white rounded-3xl shadow-2xl p-8 max-w-md w-full">
                <h1 className="text-4xl font-bold text-center mb-2 text-gray-800">
                    Prize Spinner
                </h1>
                <div className="text-center mb-6">
                    <p className="text-lg text-gray-600">Total Score: <span className="font-bold text-purple-600">{totalScore}</span></p>
                </div>

                {error && (
                    <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
                        {error}
                    </div>
                )}

                <div className="relative w-80 h-80 mx-auto mb-8">
                    {/* Pointer - Arrow pointing down */}
                    <div className="absolute top-0 left-1/2 -translate-x-1/2 -translate-y-4 z-10">
                        <div className="w-0 h-0 border-l-[20px] border-l-transparent border-r-[20px] border-r-transparent border-t-[40px] border-t-red-600 drop-shadow-2xl"></div>
                        <div className="absolute top-0 left-1/2 -translate-x-1/2 w-2 h-6 bg-red-600 -translate-y-6"></div>
                    </div>

                    {/* Spinner */}
                    <div
                        className="w-full h-full rounded-full relative overflow-hidden shadow-xl border-8 border-gray-800"
                        style={{
                            transform: `rotate(${rotation}deg)`,
                            transitionDuration: '0ms'
                        }}
                    >
                        {/* Using SVG for precise equal segments */}
                        <svg viewBox="0 0 200 200" className="w-full h-full">
                            {segments.map((segment, index) => {
                                const angle = (360 / segments.length) * index;
                                const segmentAngle = 360 / segments.length;

                                const startAngle = (angle - 90) * Math.PI / 180;
                                const endAngle = (angle + segmentAngle - 90) * Math.PI / 180;

                                const x1 = 100 + 100 * Math.cos(startAngle);
                                const y1 = 100 + 100 * Math.sin(startAngle);
                                const x2 = 100 + 100 * Math.cos(endAngle);
                                const y2 = 100 + 100 * Math.sin(endAngle);

                                const largeArc = segmentAngle > 180 ? 1 : 0;

                                const pathData = `M 100 100 L ${x1} ${y1} A 100 100 0 ${largeArc} 1 ${x2} ${y2} Z`;

                                const textAngle = (angle + segmentAngle / 2 - 90) * Math.PI / 180;
                                const textX = 100 + 60 * Math.cos(textAngle);
                                const textY = 100 + 60 * Math.sin(textAngle);
                                const textRotate = angle + segmentAngle / 2;

                                return (
                                    <g key={segment.id}>
                                        <path
                                            d={pathData}
                                            fill={segment.color}
                                            stroke="white"
                                            strokeWidth="2"
                                        />
                                        <text
                                            x={textX}
                                            y={textY}
                                            fontSize="24"
                                            fontWeight="bold"
                                            fill="#1F2937"
                                            textAnchor="middle"
                                            dominantBaseline="middle"
                                            transform={`rotate(${textRotate} ${textX} ${textY})`}
                                        >
                                            {segment.label}
                                        </text>
                                    </g>
                                );
                            })}
                        </svg>

                        {/* Center circle */}
                        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-16 h-16 bg-gray-800 rounded-full shadow-lg border-4 border-white"></div>
                    </div>
                </div>

                {result !== null && (
                    <div className="text-center mb-6 animate-bounce">
                        <p className="text-3xl font-bold text-purple-600">
                            You won {result} points! ðŸŽ‰
                        </p>
                    </div>
                )}

                <div className="flex gap-4">
                    {isSpinning ? (
                        <button
                            onClick={stopSpin}
                            disabled={!isSpinning}
                            className="flex-1 bg-red-500 hover:bg-red-600 disabled:bg-gray-400 disabled:cursor-not-allowed text-white font-bold py-4 px-6 rounded-xl text-lg transition-colors shadow-lg"
                        >
                            STOP
                        </button>
                    ) : (
                        <button
                            onClick={startSpin}
                            disabled={isSpinning}
                            className="flex-1 bg-green-500 hover:bg-green-600 disabled:bg-gray-400 disabled:cursor-not-allowed text-white font-bold py-4 px-6 rounded-xl text-lg transition-colors shadow-lg"
                        >
                            SPIN
                        </button>
                    )}
                </div>
            </div>
        </div>
    );
}